import request from '../../common/request.js'
import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
export default {
	mixins: [MescrollMixin],
	data() {
		return {
			// data:[],//全部数据
			catrgoryList: [],
			product: [], //美妆列表
			catrgory: [], //其他列表
			select_index: 0,
			makeup: true,
			page:1,//分页数
			category_id:""
		}
	},
	onLoad() {
		this.getClassification();
	},
	methods: {
		// 获取分类数据
		getClassification() {
			var data = {};
			request.post('goods/category', data).then(res => {
				this.catrgoryList = res.data
				this.chooseGoods(res.data[0].id)
			})
		},
		// 获取列表数据
		getList(data) {
			var data = data;
			request.post('search/index', data).then(res => {
				console.log(res)
				this.catrgory = res.data.data
					this.makeup = false
			})
		},
		// 选择分类
		choose(index, id) {
			if (this.select_index === index) {
				return;
			}
			this.select_index = index;
			this.chooseGoods(id)
		},
		// 分类对应商品
		chooseGoods(id) {
			var that = this;
			that.catrgory = []
			var data = that.catrgoryList
			that.category_id = id
			console.log(id)
			var arr = [];
			for (var i = 0; i < data.length; i++) {
				if (id == data[i].id) {
					var itemLength = data[i].items.length
					console.log(itemLength)
					arr = data[i].items
					if(itemLength != "11"){
						var postData={
							category_id:id,
							page:that.page
						}
						that.getList(postData)
					}else{
								that.product = arr
								that.makeup = true
					}
				}
			}
		},
		// 上拉加载和下拉刷新
		upCallback(page) {
			console.log(page)
			var data = {
				category_id: this.category_id,
				page: page.num
			};
			request.post('search/index', data).then(res => {
				var curPageData = res.data.data
				this.mescroll.endByPage(curPageData.length, res.data.page_total);
				if (page.num == 1) this.catrgory = [];
				this.catrgory = this.catrgory.concat(curPageData);
			}).catch(() => {
				//联网失败, 结束加载
				this.mescroll.endErr();
			})
		},
		// 进入美妆列表
		enterDetail(id) {
			uni.navigateTo({
				url: '/pages/productList/productList?goods_id='+id
			})
		},
		//跳转详情页
		navToDetailPage(id) {
			uni.navigateTo({
				url: '/pages/productDetails/productDetails?goods_id='+id
			})
		},
		share(){
			console.log("分享")
		}
	}
}
